            RequisitoModelo.flagObligatorio=tramiteResponse.listaRequisito[i].requisito.flagObligatorio;
            RequisitoModelo.visible=tramiteResponse.listaRequisito[i].requisito.visible;
            RequisitoModelo.ayuda='';
