            if(tramiteResponse.listaRequisito[i].ayuda==null || tramiteResponse.listaRequisito[i].ayuda==''){
               RequisitoModelo.ayuda=tramiteResponse.listaRequisito[i].ayuda;
            }else{
               RequisitoModelo.ayuda=tramiteResponse.listaRequisito[i].ayuda;
            }
            RequisitoModelo.linkFormato=tramiteResponse.listaRequisito[i].linkFormato;
            // console.log('pintando idRequisito::::' + tramiteResponse.listaRequisito[i].idRequisitoTramite);
            // console.log('pintando linkformato::::' + tramiteResponse.listaRequisito[i].linkFormato);
            RequisitoModelo.urlDescarga="https://mz-services.miraflores.gob.pe:8923/recuperarEntidadArchivo/4/"+tramiteResponse.listaRequisito[i].idTramiteRequisito+"/0";
            RequisitoModelo.urlDescargaFormato=tramiteResponse.listaRequisito[i].linkFormato;
            RequisitoModelo.idTramiteRequisito=tramiteResponse.listaRequisito[i].idTramiteRequisito;
            RequisitoModelo.textoBotonCambiar='Cambiar';
            RequisitoModelo.clase='btn btn-success';
            this.listaRequisitoModelo.push(RequisitoModelo);
            i++;
          }
          // console.log('display acciones: ' + this.displayAcciones);
          // console.log('Lista Requisito ');
          // console.log(this.listaRequisitoModelo);
          // console.log('Paso Actual: ' + this.pasoActual);
          if(this.$route.params.paso>0){
            this.pasoActual=this.$route.params.paso;
            this.CargarTabInicio();
