    retrocederPaso(){

        if(this.pasoActual==4){
          var i=0;
          var orden=0;
          var fin=0;
          var pasoCambio=0;
          while(i<this.listaPasos.length && fin==0){
            // console.log('entro a paso:');
              if(this.listaPasos[i].idPaso.idPasos==4){
                fin=1;
              }else{
                pasoCambio=this.listaPasos[i].idPaso.idPasos;
                orden=this.listaPasos[i].orden;
              }
              i++;
              // console.log('orden:'+orden+' ,paso:' + pasoCambio);
          }
          this.CambioSimple(pasoCambio,orden,-1)
        }else if(this.pasoActual==5){
          this.CambioSimple(2,1,-1)
        }else if(this.pasoActual==2 || this.pasoActual==1){
          if(this.mostrarObservaciones){
            this.CambioSimple(-1,-1,-1)
          }else{
            this.$router.push('/');
          }
        }else if(this.pasoActual==3){
          this.CambioSimple(5,2,-1);
        }
        
    },
