      cambiarPaso(){
        var nuevoPaso=0;
        var indice=0;
        var orden=0;
        var pasoActual=this.pasoActual;
        // console.log('cambiar paso 1031:' + this.pasoActual);
        while(indice<this.listaPasos.length && nuevoPaso==0){
            if(this.listaPasos[indice].orden>this.ordenActual){
              nuevoPaso=this.listaPasos[indice].idPaso.idPasos;
              orden=this.listaPasos[indice].orden;
            }
            indice++;
        }
        // console.log('cambiar paso 1039:' + pasoActual);
        if(pasoActual==2){
          if(this.permiteEditar &&this.$route.params.paso==0 ){
            // console.log('1260');
            this.ActualizarTramite(nuevoPaso,orden);
          }else{
            // console.log('1262');
            this.CambioSimple(nuevoPaso,orden,-1);
          }
        }
        if(pasoActual==5 && this.permiteEditar){
          this.$swal({
            title: this.estadoInicial==27? 'Seguro de Subsanar Solicitud?':'Seguro de Crear Solicitud?',
            text: "Luego no podr editar!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            cancelButtonText: 'No',
            confirmButtonText: 'S'
          }).then((result) => {
            if (result.value) {
              this.mensajeFinal='Se cre la solicitud de trmite ST - 00' + this.idTramite+'. ';
              this.mensajeFinal+='La solicitud ser revisada por un especialista del rea de ' + this.nombreArea;
              this.mensajeFinal+='. La notificacin de la creacin del documento o de alguna observacin le ser notificada a su correo ' + localStorage.getItem('correoNotificacionTramite');
              this.mensajeFinal+='. Recuerde que tambin puede revisar la opcin "Mis trmites" de su Plataforma Digital Miraflores para';
              this.mensajeFinal+='  hacer seguimiento a sus trmites realizados.';
              this.permiteEditar=false;

              // console.log("NUEVO PASO:"+nuevoPaso);

              // this.CambioSimple(nuevoPaso,orden,0);
            }
          });
        }else if(pasoActual==5 && !this.permiteEditar){
            // console.log("NUEVO PASO:"+nuevoPaso);
            this.CambioSimple(nuevoPaso,orden,-1);
        }
        if(pasoActual==-1){
          // console.log('cambiar paso:' + nuevoPaso + ', ' + orden);
          this.CambioSimple(nuevoPaso,orden,-1);
        }
        if(pasoActual==3){
          this.CambioSimple(nuevoPaso,orden,-1);
        }
        return nuevoPaso;
      },
      cambiarPaso1(){
        var nuevoPaso=0;
        var indice=0;
