      ActivarTab(paso,orden){
        var i=0;
        var ordenOficial=0;
        while(i<this.listaPasos.length&&ordenOficial==0){
          // console.log('pintando pasos');
          // console.log(this.listaPasos);
          if(this.listaPasos[i].idPaso.idPasos==this.pasoOficial){
            ordenOficial=this.listaPasos[i].orden;
          }
          i++;
        }
        // console.log('pintando orden oficial: '+ ordenOficial);
        // console.log('pintando orden: '+ orden);
        if(this.$route.params.paso==4){
          this.pasoActual=3;
          this.mensajeFinal='El trmite se encuentra En Revisin';
          this.CambioSimple(paso,orden,0);
        }else{
          if(orden<=ordenOficial){
            this.pasoActual=paso;
            this.CambioSimple(paso,orden,-1);
          }
        }
        
      },
      CambiarTab(nuevoPaso,orden){
        // console.log('entr a cambiar paso final');
          this.pasoActual=nuevoPaso;
          this.ordenActual=orden;
          this.activeObservacion='';
          this.activeDatosTramite='';
          this.activeArchivo='';
          this.activeFin='';
          this.activePagos='';
          this.lblContinuar='Continuar >>';
          this.lblAtras='<<Atrs';
          if(this.pasoActual==-1){
            this.activeObservacion='active';
          }
          if(this.mostrarObservaciones){
            if(this.pasoActual==-1){
              this.activeObservacion='active';
              this.lblAtras='Salir X';
            }
            if(this.pasoActual==1||this.pasoActual==2){
              this.activeDatosTramite='active';
            }
          }else{
            if(this.pasoActual==1||this.pasoActual==2){
              this.activeDatosTramite='active';
              this.lblAtras='Salir X';
            }
          }
          
          
          //if(this.pasoActual==3)this.activeFin='active';
          if(this.pasoActual==3){
            this.activePagos='active';
            this.CargarVisa();
          }
          if(this.pasoActual==4){
            this.activeFin='active';
            this.lblContinuar='Fin';
          }
          if(this.pasoActual==5)this.activeArchivo='active';
      },
      CambioSimple(nuevoPaso,orden,indicador){
        var respuesta=true;
        if(indicador>-1){
          this.$swal({
                  title: 'Guardando',
                   allowOutsideClick: false,
                   onBeforeOpen: () => {
                    this.$swal.showLoading();
                  } 
        });
          var dataPost=new FormData();
        dataPost.append('idTramite',this.idTramite);
        dataPost.append('idTipoTramite',this.idTipoTramite);
        dataPost.append('idPasoActual',this.pasoActual);
        dataPost.append('idNuevoPaso',nuevoPaso);
        dataPost.append('idEstado',this.estadoInicial);
        // console.log('nuevo paso:' + nuevoPaso);
        //var rutadev='http://localhost:8929/';
        var rutaprod='https://mz-services.miraflores.gob.pe:8090/api/tramites/';
        axios.post(Constantes.rutaTramite+'cambiarPaso/',dataPost)
                    .then(response=>{
                      // console.log('cambio de paso correcto');
                      // console.log(response.data);
                      respuesta=response.data.success;
                      if(respuesta){
                        this.$swal.close();
                        this.pasoOficial=nuevoPaso;
                        this.CambiarTab(nuevoPaso,orden);
                      }else{
                        this.permiteEditar=true;
                        this.$swal.close();
                        this.$swal({
                          icon: 'error',
                          title: 'Error',
                          text: 'Sucedi un error. Favor vuelva a intentar en unos minutos.'
                        });
                      }
                      
                  })
                  .catch(e=>console.log(e))
        }else{
          this.CambiarTab(nuevoPaso,orden);
        }
        
        
      },
      ActualizarTramite(nuevoPaso,orden){
        // console.log('ingres a actualizar trmite NUEVO PASO:' + nuevoPaso);
        var Tramite=this.LlenarObjetoTramite();
        var tramiteValidado=this.ValidarTramite(Tramite);
        var requisitosValidados=this.ValidarRequisitos();
        if(this.$route.params.idTramite>0 && this.estadoInicial!=22){
          Tramite.bloqueaActualizarEstado=true;
        }else{
          Tramite.bloqueaActualizarEstado=false;
          var Pasos={};
          Pasos.idPasos=nuevoPaso;
          Tramite.pasoActual={};
          Tramite.pasoActual=Pasos;
        }
        
        if(tramiteValidado && requisitosValidados){
          var dataPost=new FormData();
          dataPost.append('tramite',JSON.stringify(Tramite));
          //var rutadev='http://localhost:8929/';  
          //var rutaprod='https://mz-services.miraflores.gob.pe:8090/api/tramites/';
          var rutaPost=Constantes.rutaTramite+'actualizar-tramite/';
          if(Tramite.idTramite==0){
            rutaPost=Constantes.rutaTramite+'tramite-requisito/';
            dataPost.append('imgCroquis',null);
          }
          var rutaprod='http://localhost:8090/api/tramites/';
          axios.post(rutaPost,dataPost)
          //axios.post(rutadev,dataPost)
                    .then(response=>{
                      this.idTramite=response.data.data;
                      /* this.$swal({
                        icon: 'success',
                        text: 'El trmite fue registrado con xito'
                      }); */
                      this.CambioSimple(nuevoPaso,orden,-1);
                      this.ObtenerListaAdjuntos();
                      this.CargarTramite();
                      this.lblAtras='<< Atrs'
                      //console.log(this.listaTipoTramite[0].nombre);
                  })
                  .catch(e=>console.log(e))
        }
      },
      GuardarTramiteCompleto(nuevoPaso,orden,primerRequisito,indiceArchivo){
        var Tramite=this.LlenarObjetoTramite();
        var Requisito={};
        var tramiteValidado=true;
        var requisitosValidados=true;
        
        if(primerRequisito){
          requisitosValidados=true;
        }else{
          this.tramiteValidado=this.ValidarTramite(Tramite);
          this.ValidarRequisitos();
        }
        if(tramiteValidado && requisitosValidados){
          Requisito=this.LlenarObjetoRequisito(indiceArchivo);
          Tramite.listaRequisito=Requisito;
          var dataPost=new FormData();
          dataPost.append('tramite',JSON.stringify(Tramite));
          var i=0;
          while(i<this.listaRequisitosArchivos.length){
            dataPost.append('imgCroquis',this.listaRequisitosArchivos[i]);
            i++;
          }
          
          //var rutadev='http://localhost:8929/';  
          //var rutaprod='https://mz-services.miraflores.gob.pe:8090/api/tramites/';
          var rutaprod='http://localhost:8090/api/tramites/';
