      ValidarRequisitos(){
        var validado=true;
        var i=0;
        while(i<this.listaRequisitoModelo.length && validado ){
          if((this.listaRequisitoModelo[i].flagObligatorio || (i==1 && this.esAdministrado=='false' ) ) && !this.listaRequisitoModelo[i].idTramiteRequisito>0){
            this.$swal({
                  icon: 'error',
                  title: 'Error',
                  text: 'Debe Registrar Requisito: ' + this.listaRequisitoModelo[i].nombre
                });
            validado=false;
          }
          i++;
        }
        return validado;
